/*--------------------------------------------------------------
# Footer Styles
--------------------------------------------------------------*/
/*Footer Adjustment*/
body, .bricks-site-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

#menu-footer-services {
  display: grid !important;
  grid-template-columns: 1fr 1fr;
  font-size: 1.4rem; }
  @media only screen and (max-width: 479px) {
    #menu-footer-services {
      margin-left: 24px;
      margin-top: 24px; } }
  #menu-footer-services li {
    margin-left: 0;
    margin-top: 10px;
    margin-bottom: 10px; }
    #menu-footer-services li a {
      padding-right: 12px; }

#menu-footer-other {
  font-weight: 600;
  text-transform: uppercase; }
  #menu-footer-other li {
    margin-left: 0;
    margin-bottom: 32px; }

#footer-logo {
  width: 100%;
  max-width: 21.5rem; }

@media only screen and (min-width: 992px) {
  .footer-contact-icon-box__text {
    font-size: clamp(16px, calc( 16px  + (( 1vw - 10.7px) * 1.73913 )), 20px); } }

@media only screen and (max-width: 991px) {
  .footer-contact-icon-box__text {
